import React from "react";
import './DetailPage.css';
function DetailPage(props) {
  // console.log(props.location.aboutprops.name);
  const passData = props.location.aboutprops.name;
  const word = passData.word;

  const meaning = passData.meanings;

  const phonetic = passData.phonetics;

  //   console.log(passData.phonetics)
  return (
    <>
      <div className="about_word_container">
        <div className="word">{word}</div>
<div className="word_meaning_section">
        {meaning.map((mean, index) => {
          return (
            <>
              <div className="word_meaning_pos" ><span className="word_meaning_pos_heading">parts of speech :-</span>"{meaning[index].partOfSpeech}"</div>
              <div className="word_meaning_definition" ><span className="word_meaning_definition_heading">definition:-</span>{meaning[index].definitions[0].definition}</div>
              <div className="word_meaning_example" ><span className="word_meaning_example_heading">example:-</span>{meaning[index].definitions[0].example}</div>
              {/* <div >{meaning[index].definitions[0].synonyms}</div> */}
           <br /> <br />
            </>
          );
        })}
        <div className="word_meaning_synonyms">
        <span className="synonyms_heading">synonyms</span>
        {meaning[0].definitions[0].synonyms.map((val, id) => {
          return <> <span className="word_synonym"> {meaning[0].definitions[0].synonyms[id]},</span></> ;
        })}
        </div>
</div>
        <div className="word_phonetic">
          {phonetic.map((sound, key) => {
            return (
              <>
                <div className="word_phonetic_text"> <span className="word_phonetics_heading">word phonetic:-</span> "{phonetic[key].text}" </div>
                <a href={phonetic[key].audio} target="_blank" className="word_phonetic_link"><i class="fa fa-volume-up fa-2x"></i>
</a>
              </>
            );
          })}
        </div>
      </div>
    </>
  );
}

export default DetailPage;
