import React, { useState, useEffect } from "react";
import './Table.css';
import DetailPage from "./DetailPage";
import { Link } from "react-router-dom";

const Table = () => {
  const [data, setData] = useState([]);
  useEffect(() => {
    getList();
  }, []);

  const getList = () => {
    fetch("https://api.dictionaryapi.dev/api/v2/entries/en_US/hello/").then(
      (result) => {
        result.json().then((resp) => {
          setData(resp);
        });
      }
    );
  };
  return (
    <div className="container">
      {data.map((value, id) => {
        return (
          <>
            <div className="block">
          
            <h1>  Let's know more</h1>
              <div className="table">
                <table>
                  <thead>
                    <tr>
                      <th> Word</th>
                      <th> No. of Phonetics </th>
                      <th>No. of Meanings</th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr>
                      <div>
                        {" "}
                        <Link className="link"
                          to={{
                            pathname: "/detailPage",
                            aboutprops: { name: data[0] },
                          }}
                        >
                          <td className="table_word" >{data[id].word}
                          <span className="hovertext">Tap to Know More</span>
                          </td>
                        </Link>
                      </div>
                      <td className="data_phonetics_length" >{data[id].phonetics.length}</td>
                      <td className="data_meanings_length" >{data[id].meanings.length} </td>
                    </tr>
                  </tbody>
                </table>
              </div>
            </div>
          </>
        );
      })}
    </div>
  );
};

export default Table;
